
.room-code {
    height: 40px;
    border-radius: 8px;
    overflow: hidden;

    background: #fff;
    border: 1px solid #835afd;
    cursor: pointer;

    display: flex;
}

.room-code div{
    background: #835afd;
    padding: 0 12px;
    display: flex;
    justify-content: center;
    align-items: center;

}

.room-code span{
    display: block;
    align-self: center;
    flex: 1;
    padding: 0 16px 0 12px;
    width: 240px;
    font-size:14px;
    font-weight: 500;
}

.copy-img{
    margin-top: 10px;
    padding-bottom: 10px;
}