/* Arquivo global para estilização da página.*/

*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background: #f8f8f8;
    color:#29292e;
}

/* para todas essa tags ter essa formatação de texto */
body,input,button,textarea{
    font: 400 16px 'Roboto',sans-serif;
}